body {
  margin: 0;
  background: #a1ffce;
  background: -webkit-linear-gradient(to right, #faffd1, #a1ffce);
  background: linear-gradient(to right, #faffd1, #a1ffce);
}

.grid {
  width: 80%;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 2fr));
  row-gap: 2rem;
  column-gap: 5rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  margin-inline: auto;
}

.block {
  border-radius: 1rem;
  flex-flow: column;
}

.card {
  flex-grow: 1;
}

@media (max-width: 480px) {
  .card {
    font-size: 15px;
  }
}
