.card {
  height: 17rem;
  width: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;

  /* line-height: 1.8em; */

  color: white;
  background: #11998e;
  background: -webkit-linear-gradient(to right, #11998e, #38ef7d);
  background: linear-gradient(to right, #11998e, #38ef7d);
  border-radius: 1rem;
  padding: 2rem 2rem;
  text-align: center;
}

button {
  margin-top: 2rem;
  width: 15vw;
  height: 5vh;
  border-radius: 5rem;
  border: 0;
  font-size: 26px;
  color: cornflowerblue;
  transition: all 0.4s ease-in;
  box-shadow: 1px 1px 5px 3px rgb(0, 0, 0, 0.2);
  margin-bottom: 0;

  background: #faffd1;
}

button:active {
  transform: scale(0.4);
}

.text {
  font-size: 20px;
  color: gray;
}

@media (max-width: 480px) {
  .card {
    width: 15rem;
    height: 20rem;
    padding: 2rem 2rem;
    margin: auto;
    line-height: 1.5em;
  }

  button {
    width: 5rem;
  }

  .text {
    display: inline;
  }
}
